function genLevelTable(lvlCol, maxLevel, modifier = 1) {
    let result = `<thead><tr><th>${lvlCol || "lvl"}</th></tr></thead>`
    result += '<tbody>'
    for (let i = 0; i < maxLevel; i++) {
        result += `<tr><td>${i + modifier}</td></tr>\n`
    };
    result += '</tbody>';
    result = $('<div/>', {
        'class': 'gTable-lvls',
        html: `<table>${result}</table>`
    })[0]['outerHTML'];
    return result
}
// сгенерировать шапку по ключам
function genStatsTableHead(keys, objName) {
    let result = '<thead><tr>';
    for (let i = 0; i < keys.length; i++) {
        result += `<th>${keys[i]}</th>`
    }
    result += '</tr></thead>'
    return result
}
// сгенерировать тело по ключам
function genStatsTableBody(keys, data) {
    let result = '<tbody>';
    for (let i = 0; i < data.maxLevel; i++) {
        result += '<tr>';
        for (let k = 0; k < keys.length; k++) {
            let value = data[keys[k]][i];
            //console.log(data[keys[k]])
            // if (typeof value == 'object') {
            //     console.log(value)
            //     value = value[0].outerHTML
            // }
            result += `<td>${value}</td>`
        }
        result += '</tr>'
    }
    result += '</tbody>';
    return result
}
